import React, {Component} from 'react'
import { render } from 'react-dom'
import {Row, Col} from 'antd'
import { LinkedinFilled, GithubFilled } from '@ant-design/icons';
import Highcharts from 'highcharts/highstock'
import HighchartsReact from 'highcharts-react-official'

class CpStats extends Component {

    constructor(props){
        super(props);
        this.state = {
            counter: 0,
            options: {
                title: {
                    text: "CF Stats",
                    style: {
                        fontFamily: 'Arial'
                    }
                },
                series: [{
                    name: "CF Rating",
                    data: []
                }]
            }
        }
    }

    componentDidMount(){
        this.getCPStats();
    }
    
    getCPStats = () => {
        fetch('https://codeforces.com/api/user.rating?handle=jianzhi')
        .then(res => res.json()).then((result) => {
            const cf_arr = []
            for (let i = 0; i < result.result.length; i++) cf_arr.push([result.result[i].ratingUpdateTimeSeconds*1000, result.result[i].newRating]);
            this.setState({options: {
                title: {
                    text: "CF Stats"
                },
                series: [{
                    data: cf_arr
                }]
            }});
        });
    }
	
	render(){
        return (
            <div>
                <Row><h2>CF Stats</h2></Row>
                <Row>
                    <HighchartsReact
                        highcharts={Highcharts}
                        options={this.state.options}
                        constructorType={'stockChart'}
                        ref={this.chartComponent}
                        allowChartUpdate={true}
                    />
                </Row>
            </div>
        );
	}
	
}

export default CpStats;