import './Page.css';
import 'antd/dist/antd.css';
import React, {Component} from 'react'
import { Layout, Menu, Breadcrumb } from 'antd';
import { UserOutlined, HomeOutlined, RadarChartOutlined, DribbbleOutlined, ApartmentOutlined, LaptopOutlined, SmileOutlined, NotificationOutlined, EuroOutlined, GiftOutlined, RocketOutlined, StockOutlined } from '@ant-design/icons';
import ContentCom from './ContentCom'
const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;


class Page extends Component {

    constructor(props){
        super(props);
        this.state = {
            counter: 0,
            pageName: "home"
        }
    }

    handleMenuClick = (e) => {
        console.log('click', e)
        this.setState({pageName: e.key})
    }
	
	render(){
		return (
            <div style={{height:"100%"}}>
            <Layout style={{height:"100%"}}>
                <Header className="header">
                    <div className="logo" />
                    <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['2']}>
                        <Menu.Item key="1">Home</Menu.Item>
                        <Menu.Item key="2">Pages</Menu.Item>
                        <Menu.Item key="3">Under Construction</Menu.Item>
                    </Menu>
                </Header>
                <Layout>
                    <Sider width={200} className="site-layout-background">
                    <Menu
                        mode="inline"
                        defaultSelectedKeys={['1']}
                        defaultOpenKeys={['sub1']}
                        style={{ height: '100%', borderRight: 0 }}
                        onClick={this.handleMenuClick}
                    >
                        <Menu.Item title="Home" icon={<HomeOutlined />} key="home">Home</Menu.Item>
                        <SubMenu key="life" icon={<SmileOutlined />} title="Life">
                            <Menu.Item key="weather">Weather</Menu.Item>
                        </SubMenu>
                        <SubMenu key="work" icon={<LaptopOutlined />} title="Work">
                            <Menu.Item key="5">option5</Menu.Item>
                            <Menu.Item key="6">option6</Menu.Item>
                        </SubMenu>
                        <SubMenu key="sports" icon={<DribbbleOutlined />} title="Sports">
                            <Menu.Item key="running">Running</Menu.Item>
                        </SubMenu>
                        <SubMenu key="stocks" icon={<StockOutlined />} title="Stocks">
                            <Menu.Item key="getstock">Get Stock</Menu.Item>
                            <Menu.Item key="4">option6</Menu.Item>
                            <Menu.Item key="7">option7</Menu.Item>
                            <Menu.Item key="8">option8</Menu.Item>
                        </SubMenu>
                        <SubMenu key="maths" icon={<RadarChartOutlined />} title="Mathematics">
                            <Menu.Item key="mnotes">Notes</Menu.Item>
                        </SubMenu>
                        <SubMenu key="cp" icon={<ApartmentOutlined />} title="CP">
                            <Menu.Item key="cpstats">Stats</Menu.Item>
                        </SubMenu>
                        <SubMenu key="physics" icon={<RocketOutlined />} title="Physics">
                            <Menu.Item key="olympiadnotes">Olympiad Notes</Menu.Item>
                        </SubMenu>
                    </Menu>
                    </Sider>
                    <Layout style={{ padding: '0 24px 24px' }}>

                    <Content
                        className="site-layout-background"
                        style={{
                        padding: 24,
                        margin: 0,
                        minHeight: 280,
                        }}
                    >
                        <ContentCom pageName={this.state.pageName}/>
                    </Content>
                    </Layout>
                </Layout>
            </Layout>
            </div>
		);
	}
	
}

export default Page;