import React, {Component} from 'react'
import OlympiadNotes from './pages/OlympiadNotes'
import GetStock from './pages/GetStock'
import HomePage from './pages/HomePage'
import CpStats from './pages/CpStats'
import Weather from './pages/Weather'

class ContentCom extends Component {

    constructor(props){
        super(props);
        this.state = {
            counter: 0
        }
    }
	
	render(){
        if (this.props.pageName=="olympiadnotes") return (<OlympiadNotes/>);
        if (this.props.pageName=="getstock") return (<GetStock/>);
        if (this.props.pageName=="home") return (<HomePage/>);
        if (this.props.pageName=="cpstats") return (<CpStats/>);
        if (this.props.pageName=="weather") return (<Weather/>);
		return (
            <div>
                404: Content Not Found
            </div>
		);
	}
	
}

export default ContentCom;