import React, {Component} from 'react'
import { Row, Col } from 'antd';
import { AgGridColumn, AgGridReact } from 'ag-grid-react';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-balham.css';

class OlympiadNotes extends Component {

    constructor(props){
        super(props);
        this.state = {
            counter: 0,
            columnDefs: [
                {headerName:"Topic", field:"topic"},
                {headerName:"Lecture Notes", field:"notes", cellRenderer: function(params){return ('<a href="https://www.google.com" target="_blank">' + params.value + '</a>')}},
                {headerName:"Solutions", field:"solution", cellRenderer: function(params){return ('<a href="https://www.google.com" target="_blank">' + params.value + '</a>')}}
            ],
            rowData: [
                {topic: "Mechanics I", notes: "link", solution: "link"},
                {topic: "Mechanics II", notes: "link", solution: "link"},
                {topic: "Mechanics III", notes: "link", solution: "link"},
                {topic: "Waves", notes: "link", solution: "link"},
                {topic: "Electricity and Magnetism I", notes: "link", solution: "link"},
                {topic: "Electricity and Magnetism II", notes: "link", solution: "link"},
                {topic: "Fluid Dynamics", notes: "link", solution: "link"},
                {topic: "Thermodynamics", notes: "link", solution: "link"},
                {topic: "Special Relativity", notes: "link", solution: "link"},
                {topic: "Quantum Mechanics", notes: "link", solution: "link"},
            ]
        }
    }

    onGridReady = (params) => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }
	
	render(){
		return (
            <div>
                <Row>
                    <h2>Olympiad Notes</h2>
                </Row>
                <Row>
                    <h4>
                        I used to teach Physics Olympiad. Below are the notes which I made.
                    </h4>
                </Row>
                <br></br>
                <Row>
                    <div className="ag-theme-balham" style={{height:'340px', width:'600px'}}>
                        <AgGridReact
                            onGridReady={this.onGridReady}
                            columnDefs={this.state.columnDefs}
                            rowData={this.state.rowData}
                        />
                    </div>
                </Row>
            </div>
		);
	}
	
}

export default OlympiadNotes;