import React, {Component} from 'react'
import { Animator, ScrollContainer, ScrollPage, batch, Fade, FadeIn, Move, MoveIn, MoveOut, Sticky, StickyIn, ZoomIn } from "react-scroll-motion";
import { Button } from 'antd'
class Home extends Component {

	constructor(props){
		super(props);
	}
	
	render(){
		const ZoomInScrollOut = batch(StickyIn(), FadeIn(), ZoomIn());
		const FadeUp = batch(Fade(), Move(), Sticky());
		return (
			<ScrollContainer>
				<ScrollPage page={0}>
					<Animator animation={batch(Fade(), Sticky(), MoveOut(0, -200))}>
						<span style={{ fontSize: "4em" }}>Hello! I am Wang Jianzhi üòÄ</span>
						<br></br>
						<span style={{ fontSize: "3em" }}>Nice to meet you! üëãüèª</span>
						<h6>Scroll down for more</h6>
					</Animator>
				</ScrollPage>
				<ScrollPage page={1}>
					<Animator animation={ZoomInScrollOut}>
						<span style={{ fontSize: "3em" }}>Welcome to my website ‚ú®</span>
					</Animator>
				</ScrollPage>
				<ScrollPage page={2}>
					<Animator animation={batch(Fade(), Sticky())}>
						<span style={{ fontSize: "3em" }}>Press to continue!</span>
						<Button type="text" onClick={this.props.toggleHome}>{"> Proceed"}</Button>
					</Animator>
				</ScrollPage>
			</ScrollContainer>
		);
	}
	
}

export default Home;

