import React, {Component} from 'react'
import {Row, Col} from 'antd'
import { LinkedinFilled, GithubFilled } from '@ant-design/icons';

class HomePage extends Component {

    constructor(props){
        super(props);
        this.state = {
            counter: 0
        }
    }
	
	render(){
        return (
            <div>
                <Row><h2>Hi. I am Wang Jianzhi.</h2></Row>
                <Row>Welcome.</Row>
                <Row>Take a look at some of the pages on the left.</Row>
                <Row>
                    <Col span={1}><LinkedinFilled/></Col>
                    <Col span={1}><GithubFilled/></Col>
                </Row>
            </div>
        );
	}
	
}

export default HomePage;