import './Weather.css';
import React, {Component} from 'react'
import {Row, Col} from 'antd'
import { LinkedinFilled, GithubFilled } from '@ant-design/icons';

class Weather extends Component {

    constructor(props){
        super(props);
        this.state = {
            counter: 0
        }
    }

    componentDidMount(){
        this.getWeather();
    }

    componentDidUpdate(){

    }

    getWeather = () => {
        fetch('https://wttr.in/Singapore?0')
        .then(res => res.text()).then((result) => {
            console.log(result)
            result = result.split("pre>")
            console.log(result)
            console.log(result.length)
            var x = result[result.length - 2]
            console.log(x)
            x = x.substring(0, x.length - 2)
            x = "<pre>" + x + "</pre>"
            this.setState({
                weather: x
            })
            console.log(x)
            //const cf_arr = []
            //for (let i = 0; i < result.result.length; i++) cf_arr.push([result.result[i].ratingUpdateTimeSeconds*1000, result.result[i].newRating]);
            //this.setState({options: {
            //    title: {
            //        text: "CF Stats"
            //    },
            //    series: [{
            //        data: cf_arr
            //    }]
            //}});
        });
    }
	
	render(){
        return (
            <div>
                <Row><h2>Weather</h2></Row>
                <Row>
                    <div dangerouslySetInnerHTML={{__html: this.state.weather}}></div>
                </Row>
            </div>
        );
	}
	
}

export default Weather;